<template>
  <v-dialog v-model="dialog.show" persistent max-width="950px">
    <v-card>
      <v-toolbar dark color="#f5f5f5" dense>
        <div class="title" style="color: black">
          {{ $vuetify.lang.t("$vuetify.patients_settlements.new.title") }}
        </div>
        <v-spacer></v-spacer>
        <v-btn icon light @click="forceRefresh() + resetDataForm()">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-form @submit.prevent="save">
        <v-card-text max-width="1500px">
          <v-container>
            <v-row wrap>
              <v-col cols="12" sm="3" class="mb-0 py-0">
                <v-text-field
                    v-model="form.code"
                    color="#007bff"
                    :label="
                    $vuetify.lang.t(
                      '$vuetify.patients_settlements.cols.code.title'
                    )
                  "
                    prepend-inner-icon="mdi-code-array mdi-dark mdi-18px"
                    class="font-weight-bold"
                    outlined
                    dense
                    filled
                    readonly
                ></v-text-field> </v-col
              ><v-col cols="12" sm="1" class="mb-0 py-0"> </v-col>
              <v-col cols="12" sm="4" class="mb-0 py-0">
                <v-menu
                    v-model="menu3"
                    color="#007bff"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    transition="scale-transition"
                    offset-y
                    min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                        v-model="form.date"
                        color="#007bff"
                        :label="
                        $vuetify.lang.t(
                          '$vuetify.patients_settlements.cols.date.title'
                        )
                      "
                        prepend-inner-icon="mdi-clock-start mdi-dark mdi-18px"
                        class="font-weight-bold"
                        outlined
                        v-bind="attrs"
                        v-on="on"
                        dense
                    ></v-text-field>
                  </template>
                  <v-date-picker
                      v-model="form.date"
                      color="#007bff"
                      @input="menu3 = false"
                  ></v-date-picker>
                </v-menu>
              </v-col>
              <v-col cols="12" sm="1" class="mb-0 py-0"> </v-col>
              <v-col cols="12" sm="3" class="mb-0 py-0">
                <v-text-field
                    v-model="$auth.user().username"
                    color="#007bff"
                    :label="
                    $vuetify.lang.t(
                      '$vuetify.patients_settlements.cols.cashier.title'
                    )
                  "
                    prepend-inner-icon="mdi-code-array mdi-dark mdi-18px"
                    class="font-weight-bold"
                    outlined
                    dense
                    filled
                    readonly
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="4" class="mb-0 py-0">
                <v-text-field
                    color="#007bff"
                    :label="
                    $vuetify.lang.t('$vuetify.bills.cols.code_patient.title')
                  "
                    prepend-inner-icon="mdi-code-array mdi-dark mdi-18px"
                    class="font-weight-bold"
                    outlined
                    dense
                    v-model="get_patient_code"
                    filled
                    readonly
                ></v-text-field>
              </v-col>

              <v-col cols="12" sm="4" class="mb-0 py-0">
                <v-autocomplete
                    v-model="form.patient"
                    :items="itemsPatient"
                    :loading="isLoadingPatient"
                    :search-input.sync="searchPatient"
                    return-object
                    color="#007bff"
                    item-text="name"
                    item-value="id"
                    :label="
                    $vuetify.lang.t(
                      '$vuetify.patients_settlements.cols.patient_name.title'
                    )
                  "
                    prepend-inner-icon="mdi-account mdi-dark mdi-18px"
                    class="font-weight-bold"
                    outlined
                    dense
                    clearable
                    @change="getItemsDetails()"
                ></v-autocomplete>
              </v-col>
              <v-col cols="12" sm="4" class="mb-0 py-0">
                <v-text-field
                    color="#007bff"
                    v-model="get_total_balance"
                    :label="
                    $vuetify.lang.t(
                      '$vuetify.patients_settlements.cols.patient_balance.title'
                    )
                  "
                    prepend-inner-icon="mdi-scale-balance mdi-dark mdi-18px"
                    class="font-weight-bold"
                    outlined
                    dense
                    suffix="FCFA"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="3" class="mb-0 py-0">
                <v-text-field
                    color="#007bff"
                    v-model="form.payment"
                    :label="
                    $vuetify.lang.t(
                      '$vuetify.patients_settlements.cols.payment.title'
                    )
                  "
                    prepend-inner-icon="mdi-credit-card mdi-dark mdi-18px"
                    class="font-weight-bold"
                    outlined
                    dense
                    suffix="FCFA"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="3" class="mb-0 py-0">
                <v-text-field
                    color="#007bff"
                    :value="get_advances"
                    :label="
                    $vuetify.lang.t(
                      '$vuetify.patients_settlements.cols.sum_remaining.title'
                    )
                  "
                    prepend-inner-icon="mdi-credit-card mdi-dark mdi-18px"
                    class="font-weight-bold"
                    outlined
                    dense
                    readonly
                    suffix="FCFA"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="5" class="mb-0 py-0">
                <v-text-field
                    v-model="form.wordings"
                    color="#007bff"
                    :label="
                    $vuetify.lang.t(
                      '$vuetify.patients_settlements.cols.wordings.title'
                    )
                  "
                    prepend-inner-icon="mdi-script-text mdi-dark mdi-18px"
                    class="font-weight-bold"
                    outlined
                    dense
                    :readonly="supply"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="1" class="mb-0 py-0">
                <v-btn
                    color="#007bff"
                    :loading="loadingBtn"
                    dark
                    type="submit"
                    dense
                    max-width="2px"
                    style="margin-top: 2px"
                    depressed
                    :disabled="
                    this.form.payment == null || this.form.payment === 0
                  "
                >
                  <v-icon dark>
                    mdi-content-save
                  </v-icon>
                  <!--{{ $vuetify.lang.t(messages.submit) }}-->
                </v-btn>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <!--<v-card-actions style="margin-top: -45px">-->
        <!--<v-spacer></v-spacer>-->
        <!--<v-btn color="#007bff" :loading="loading" dark type="submit" small style="margin-left: 18px">-->
        <!--{{ $vuetify.lang.t(messages.submit) }}-->
        <!--</v-btn>-->
        <!--</v-card-actions>-->
        <v-divider class="mx-4" style="margin-top: -30px"></v-divider>
      </v-form>
      <div>
        <v-card max-width="900px" style="margin-left: 18px">
<!--          <div class="mt-5 mx-5">-->
<!--            <v-btn-->
<!--                :loading="loadingDetails.refresh"-->
<!--                @click="refreshItemsDetail()"-->
<!--                class="mr-2 my-2"-->
<!--                depressed-->
<!--                color="#f1f5fc"-->
<!--                dense-->
<!--            >-->
<!--              <v-icon left color="#007bff">mdi-refresh</v-icon>-->
<!--              {{ $vuetify.lang.t("$vuetify.btn.refresh") }}-->
<!--            </v-btn>-->
<!--            <v-btn-->
<!--                color="#f1f5fc"-->
<!--                @click="showFilter()"-->
<!--                class="mr-2 my-2"-->
<!--                depressed-->
<!--                dense-->
<!--            >-->
<!--              <v-icon color="#007bff" left>mdi-filter-outline</v-icon>-->
<!--              {{ $vuetify.lang.t("$vuetify.supplies.btn.filter.title") }}-->
<!--            </v-btn>-->
<!--            <v-spacer></v-spacer>-->
<!--          </div>-->
          <v-data-table
              :footer-props="{
              'items-per-page-options': itemPerPageOptions
            }"
              :headers="headers"
              :items="items"
              :items-per-page="itemPerPage"
              :loading="loadingDetails.list"
              :options.sync="pagination"
              :server-items-length="meta.totalElements"
              :sort-by="sortBy"
              :sort-desc="sortDesc"
              item-key="name"
              class="mt-5"
          >
            <template v-slot:header.bill_N="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <template v-slot:header.arrival_date="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr> </template
            ><template v-slot:header.bill_type="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
            <template v-slot:header.date_bill="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <template v-slot:header.net_payable="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr> </template
            ><template v-slot:header.advance="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr> </template
          ><template v-slot:header.balance="{ header }">
            <tr>
              {{
                $vuetify.lang.t(header.text)
              }}
            </tr>
          </template>
<!--            <template v-slot:header.null="{ header }">-->
<!--              <tr>-->
<!--                {{-->
<!--                  $vuetify.lang.t(header.text)-->
<!--                }}-->
<!--              </tr>-->
<!--            </template>-->
            <template v-slot:body="{ items }">
              <v-hover>
                <tbody>
                <tr :key="item.id" v-for="item in items">
                  <td class="subtitle-2">{{ item.code }}</td>
                  <td class="subtitle-2">
                    {{ item.bill_type }}
                  </td>
                  <td class="subtitle-2">
                    {{ $moment(item.createdAt).format("YYYY-MM-DD") }}
                  </td>
                  <td class="subtitle-2">
                    {{ item.net_payable }}
                  </td>
                  <td class="subtitle-2">
                    {{ item.advance }}
                  </td>
                  <td class="subtitle-2">
                    {{ item.balance }}
                  </td>
                </tr>
                </tbody>
              </v-hover>
            </template>
          </v-data-table>
          <delete-item
              :dialog="dialogDelete"
              :messages="messagesDelete"
              :urlItems="urlDeleteItems"
              @getItemsDetails="getItemsDetails"
          ></delete-item>
          <!--          <form-details-->
          <!--            :dialog="dialogFormDetails"-->
          <!--            :form="form"-->
          <!--            :messages="messages"-->
          <!--            @getItems="getItemsDetails"-->
          <!--            ref="detailsForm"-->
          <!--          ></form-details>-->
        </v-card>
      </div>
    </v-card>
  </v-dialog>
</template>

<script>
// import ListMixin from "./../../mixins/Common/List.vue";
import FormMixin from "./../../mixins/Common/Form.vue";
// import {mapGetters} from "vuex";
// import { required } from "vuelidate/lib/validators";
import DeleteItem from "./../Common/DeleteDetails";

export default {
  mixins: [FormMixin],
  props: {
    form: {
      type: Object,
      default: function() {
        return {
          id: null,
          patient: null,
          patient_name: null,
          patient_balance: null,
          payment: null,
          date: null,
          wordings: null,
          sum_remaining: null,
          code: null,
          bills: null,
          cashier: null
        };
      }
    }
  },
  data: () => ({
    formData: {
      id: null,
      name: null,
      coefficient: null,
      number_account: null,
      quote_internal: null,
      quote_external: null
    },
    headers: [],
    items: [],
    meta: {},
    itemPerPage: 10,
    //itemPerPages: 3,
    itemPerPageOptions: [5, 10, 30],
    urlDeleteItems: "",
    sortDesc: true,
    loading: {
      list: false,
      refresh: false,
      filter: false
    },
    pagination: {},
    selectedItem: false,
    _search: {},
    _sorts: {},
    dialogDelete: {
      show: false
    },
    sortBy: ["id"],
    e1: 1,
    isLoadingPatient: false,
    isLoading: false,
    search: null,
    searchPatient: null,
    menu2: false,
    supply: false,
    active: false,
    menu3: false,
    menu4: false,
    menu: false,
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    dialogFormDetails: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    messagesDelete: {
      success: "$vuetify.supplies.delete.success"
    },
    loadingBtn: false,
    // formR: {
    //   id: null,
    //   name: null
    // },
    loadingDetails: {
      list: false,
      refresh: false,
      filter: false
    },
    itemsPatient: [],
    urlPatient: "patients",
    urlItems: "patients_settlements",
    urlItemsDetails_patients_settlements:
        "patients_settlements/details_patients_settlements"
  }),
  watch: {
    pagination: {
      // handler() {
      //   this.getItemsDetails();
      // },
      deep: true
    },
    search(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoading = true;
      this.$store
          .dispatch("request", {
            url: self.urlProduct,
            params: params
          })
          .then(response => {
            self.itemsProduct = response.data.content;
            // console.log(this.form.product);
          })
          .finally(() => (this.isLoading = false));
    },
    searchPatient(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoadingPatient = true;
      this.$store
          .dispatch("request", {
            url: self.urlPatient,
            params: params
          })
          .then(response => {
            // console.log("respp", response.data.content);
            self.itemsPatient = response.data.content;
          })
          .finally(() => (this.isLoadingPatient = false));
    }
  },
  created() {
    this.setHeaders();

    //this.setItemRoles();
  },
  validations() {
    let validators = {
      form: {}
    };
    return validators;
  },
  methods: {
    editBills(item) {
      this.form.patient_balance = item.balance;
      this.form.net_payable = item.net_payable;
      this.form.bills = item.id;
    },
    forceRefresh() {
      this.dialog.show = false;
      this.items = [];
      this.active = false;
    },
    deleteItem(item) {
      this.urlDeleteItems =
          this.urlItemsDetails_patients_settlements + "/" + item.id;
      this.dialogDelete.show = true;
    },
    setHeaders() {
      this.headers = [
        {
          text: "$vuetify.bills.cols.bill_N.title",
          value: "bill_N",
          align: "start",
          sortable: false,
          width: "15",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.bills.cols.bill_type.title",
          value: "bill_type",
          align: "start",
          sortable: false,
          width: "50",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.bills.cols.date_bill.title",
          value: "date_bill",
          align: "start",
          sortable: false,
          width: "50",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.bills.cols.net_payable.title",
          value: "net_payable",
          align: "start",
          sortable: false,
          width: "6",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.bills.cols.advance.title",
          value: "advance",
          align: "start",
          sortable: false,
          width: "6",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.bills.cols.balance.title",
          value: "balance",
          align: "start",
          sortable: false,
          width: "6",
          class: "grey--text text--darken-3"
        },
        // {
        //   text: "$vuetify.supplies.cols.created.title",
        //   value: "create_date",
        //   align: "start",
        //   sortable: false,
        //   width: "105",
        //   class: "grey--text text--darken-3"
        // },
        // {
        //   text: "$vuetify.supplies.cols.actions.title",
        //   value: null,
        //   align: "start",
        //   sortable: false,
        //   width: "20"
        // }
      ];
    },

    // Fonction pour rcuperer le contenu des champs du formulaire
    getDataForm() {
      let data = {
        patient_balance: this.get_advances,
        payment: this.form.payment,
        date: this.form.date,
        wordings: this.form.wordings,
        bills: this.form.bills,
        patient: this.form.patient["id"],
        cashier: this.$auth.user().id
      };
      return data;
    },
    setForm(item) {
      // Donnes envoyes lors de la modification
      this.form.id = item.id;
      this.form.patient = item.id;
      this.form.reference_no = item.reference_no;
      this.form.arrival_date = item.arrival_date;
      this.form.supply_amount = item.supply_amount;
      this.form.additional_info = item.additional_info;
      this.form.code = item.code;
      this.form.patient = item.patient["id"];
      this.itemsPatient.push(item.patient);
      // console.log("cul", this.form.supplies);
    },
    getRoute() {
      if (this.form.id) {
        return this.urlItems + "/" + this.form.id;
      } else {
        return this.urlItems;
      }
    },
    refreshItemsDetail() {
      let self = this;
      this.loadingDetails.refresh = true;
      this.getItemsDetails()
          .then(() => {})
          .catch(() => {})
          .then(() => {
            self.loadingDetails.refresh = false;
          });
    },
    save() {
      this.$store.dispatch("showNotification", {
        statut: false
      });
      let self = this;
      self.$v.$touch();
      if (!self.$v.$invalid) {
        this.loading = true;
        this.$store
            .dispatch("request", {
              url: self.getRoute(),
              method: self.getMethod(),
              data: self.getDataForm()
            })
            .then(response => {
              self.$store.dispatch("showNotification", {
                statut: true,
                text: self.$vuetify.lang.t(self.messages.success)
              });
              self.$emit("getItems");
              this.form.patients_settlements = response.data["id"];
              this.form.code = response.data["code"];
              // console.log(this.form.id_supplies);
              this.supply = true;
              this.active = true;
            })
            .catch(error => {
              let message;
              if (error.response.status === 400) {
                message = "$vuetify.error_occured";
                const fields = error.response.data;
                self.setFormErrors(fields);
                this.$forceUpdate();
              } else {
                message = "$vuetify.error_server";
              }
              self.$store.dispatch("showNotification", {
                statut: true,
                text: self.$vuetify.lang.t(message),
                color: "#B71C1C"
              });
            })
            .then(() => {
              self.loading = false;
            });
      }
      /*else {
               console.log(self.$v);
             }*/
    },
    getItemsDetails() {
      let self = this;
      const { page, itemsPerPage } = this.pagination;
      // console.log(page);
      // console.log(itemsPerPage);
      let params = {
        page: page,
        size: itemsPerPage
      };
      if (!this._.isEmpty(this._search)) {
        this._search = this._.merge(params, this._search);
      }
      return new Promise((resolve, reject) => {
        this.loadingDetails.list = true;
        //console.log("ls params", params);
        //console.log("Sorts", sortBy, sortDesc);
        self.$store
            .dispatch("request", {
              url:
                  self.urlItemsDetails_patients_settlements +
                  "?patient=" +
                  this.form.patient["id"],
              params: params
            })
            .then(response => {
              let data = response.data;
              self.items = data;
              self.meta = { totalElements: data.totalElements };
              console.log("meta", response);
              resolve(response);
            })
            .catch(err => {
              let message = this.$vuetify.lang.t("$vuetify.error_occured");
              if (err.response) {
                if (err.response.status === 400) {
                  self.setApiFormErrors(err.response.data.data);
                  message = self.$vuetify.lang.t("$vuetify.error_form");
                } else if (err.response.status === 403) {
                  message = self.$vuetify.lang.t("$vuetify.error_denied");
                } else if (err.response.status === 500) {
                  message = self.$vuetify.lang.t("$vuetify.error_server");
                }
              }
              self.$store.dispatch("showNotification", {
                status: true,
                text: message
              });
              reject(err);
            })
            .then(() => {
              self.loadingDetails.list = false;
              resolve();
            });
      });
    }
  },
  computed: {
    get_total_balance() {
      let total = 0;
      this.items.forEach(d => {
        total += d.balance;
      });
      // let sum = this.items.reduce((a, b) => a + (b[key] || 0), 0)
      // console.log("hum",total);
      return total;
    },
    get_patient_code() {
      let patient_code = "";
      patient_code = this.form.patient === null ? "" : this.form.patient?.code;
      return patient_code;
    },
    get_advances() {
      // console.log("hum",this.form.product.purchase_price);
      if (this.form.payment === undefined) {
        return 0;
      } else {
        let balances;
        balances = this.get_total_balance - this.form.payment;
        return balances;
      }
    },
    nameErrors() {
      let errors = [];
      if (!this.$v.form.name.$dirty) return errors;
      !this.$v.form.name.required &&
      errors.push(
          this.$vuetify.lang.t("$vuetify.supplies.cols.name.required")
      );
      // !this.$v.form.name.unique &&
      //   errors.push(
      //     this.$vuetify.lang.t("$vuetify.supplies.cols.name.unique")
      //   );
      return errors;
    },
    reference_noErrors() {
      let errors = [];
      if (!this.$v.form.reference_no.$dirty) return errors;
      !this.$v.form.reference_no.required &&
      errors.push(
          this.$vuetify.lang.t("$vuetify.supplies.cols.reference_no.required")
      );
      return errors;
    },
    total_amountErrors() {
      let errors = [];
      if (!this.$v.form.total_amount.$dirty) return errors;
      !this.$v.form.total_amount.required &&
      errors.push(
          this.$vuetify.lang.t("$vuetify.supplies.cols.total_amount.required")
      );
      return errors;
    }
  },
  components: {
    DeleteItem
  }
};
</script>
