<template>
  <v-dialog
    v-model="dialog.show"
    persistent
    fullscreen
    hide-overlay
    transition="dialog-bottom-transition"
  >
    <v-card>
      <v-toolbar dark color="#f5f5f5" dense>
        <div class="title" style="color: black">
          {{ $vuetify.lang.t(messages.title) }}
        </div>
        <v-spacer></v-spacer>
        <template v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])">
          <v-btn
            class="mr-2 my-2"
            color="#007bff"
            @click="createD()"
            dark
            dense
            small
          >
            <v-icon dark>
              mdi-plus
            </v-icon>
            {{ $vuetify.lang.t("$vuetify.cash_movement.title") }}
          </v-btn>
        </template>
        <v-spacer></v-spacer><v-spacer></v-spacer>
        <template v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])">
          <v-btn
            class="mr-2 my-2"
            color="#007bff"
            @click="createP()"
            dark
            dense
            small
          >
            <v-icon dark>
              mdi-plus
            </v-icon>
            {{ $vuetify.lang.t("$vuetify.patients_settlements.title") }}
          </v-btn>
        </template>
        <v-spacer></v-spacer>
        <v-btn icon light @click="forceRefresh() + resetDataForm()">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-form style="margin-top: 15px">
        <v-card-text>
          <v-row wrap>
            <v-col cols="12" sm="3" class="mb-0 py-0">
              <v-text-field
                v-model="form.code"
                color="#007bff"
                :label="$vuetify.lang.t('$vuetify.bills.cols.bill_N.title')"
                prepend-inner-icon="mdi-code-array mdi-dark mdi-18px"
                class="font-weight-bold"
                outlined
                dense
                filled
                readonly
              ></v-text-field> </v-col
            ><v-col cols="12" sm="1" class="mb-0 py-0"> </v-col>
            <v-col cols="12" sm="2" class="mb-0 py-0">
              <v-menu
                v-model="menu3"
                color="#007bff"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="form.bills_date"
                    color="#007bff"
                    :label="
                      $vuetify.lang.t('$vuetify.bills.cols.date_bill.title')
                    "
                    prepend-inner-icon="mdi-clock-start mdi-dark mdi-18px"
                    class="font-weight-bold"
                    outlined
                    v-bind="attrs"
                    v-on="on"
                    dense
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="form.bills_date"
                  color="#007bff"
                  @input="menu3 = false"
                ></v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="12" sm="3" class="mb-0 py-0">
              <v-select
                v-model="form.bill_type"
                color="#007bff"
                :items="optionBills.type_bills"
                :label="$vuetify.lang.t('$vuetify.bills.cols.bill_type.title')"
                :placeholder="
                  $vuetify.lang.t('$vuetify.bills.cols.bill_type.title')
                "
                prepend-inner-icon="mdi-gender-male-female mdi-dark mdi-18px"
                class="font-weight-bold"
                outlined
                dense
              ></v-select>
            </v-col>
            <v-col cols="12" sm="3" class="mb-0 py-0">
              <v-text-field
                v-model="$auth.user().username"
                color="#007bff"
                :label="$vuetify.lang.t('$vuetify.bills.cols.cashier.title')"
                prepend-inner-icon="mdi-code-array mdi-dark mdi-18px"
                class="font-weight-bold"
                outlined
                dense
                filled
                readonly
              ></v-text-field>
            </v-col>

            <v-col cols="12" sm="4" class="mb-0 py-0">
              <v-autocomplete
                v-model="form.patient"
                :items="itemsPatient"
                :loading="isLoadingPatient"
                :search-input.sync="searchPatient"
                color="#007bff"
                item-text="name"
                item-value="id"
                return-object
                :label="
                  $vuetify.lang.t('$vuetify.bills.cols.patient_name.title')
                "
                prepend-inner-icon="mdi-account mdi-dark mdi-18px"
                class="font-weight-bold"
                outlined
                dense
                clearable
                :readonly="supply"
              ></v-autocomplete>
            </v-col>
            <v-col cols="12" sm="4" class="mb-0 py-0">
              <v-text-field
                color="#007bff"
                :label="
                  $vuetify.lang.t('$vuetify.bills.cols.code_patient.title')
                "
                prepend-inner-icon="mdi-code-array mdi-dark mdi-18px"
                class="font-weight-bold"
                outlined
                dense
                v-model="get_patient_code"
                readonly
                filled
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="1" class="mb-0 py-0">
              <v-btn
                class="mr-8"
                dark
                dense
                @click="createPatient()"
                color="#007bff"
              >
                <v-icon dark>
                  mdi-plus
                </v-icon>
              </v-btn>
            </v-col>
            <v-col cols="12" sm="3" class="mb-0 py-0">
              <v-text-field
                color="#007bff"
                :label="
                  $vuetify.lang.t('$vuetify.bills.cols.patient_type.title')
                "
                prepend-inner-icon="mdi-shape-circle-plus mdi-dark mdi-18px"
                class="font-weight-bold"
                outlined
                dense
                v-model="get_patient_shape"
                readonly
                filled
              ></v-text-field>
            </v-col>
            <!--            <v-col cols="12" sm="3" class="mb-0 py-0">-->
            <!--              <v-select-->
            <!--                v-model="form.patient_type"-->
            <!--                color="#007bff"-->
            <!--                :items="optionPatient_shape.type_Patient_shape"-->
            <!--                :label="-->
            <!--                  $vuetify.lang.t('$vuetify.bills.cols.patient_type.title')-->
            <!--                "-->
            <!--                :placeholder="-->
            <!--                  $vuetify.lang.t('$vuetify.bills.cols.patient_type.title')-->
            <!--                "-->
            <!--                prepend-inner-icon="mdi-gender-male-female"-->
            <!--                class="font-weight-bold mdi-18px"-->
            <!--                outlined-->
            <!--                dense-->
            <!--              ></v-select>-->
            <!--            </v-col>-->

            <v-col cols="12" sm="4" class="mb-0 py-0">
              <v-text-field
                color="#007bff"
                v-model="form.patient_balance"
                :label="
                  $vuetify.lang.t('$vuetify.bills.cols.patient_balance.title')
                "
                prepend-inner-icon="mdi-credit-card mdi-dark mdi-18px"
                class="font-weight-bold"
                outlined
                dense
                suffix="FCFA"
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="4" class="mb-0 py-0">
              <v-autocomplete
                v-model="form.doctor"
                :items="itemsDoctor"
                :loading="isLoadingDoctor"
                :search-input.sync="searchDoctor"
                color="#007bff"
                item-text="name"
                item-value="id"
                return-object
                :label="
                  $vuetify.lang.t('$vuetify.bills.cols.dealing_with.title')
                "
                prepend-inner-icon="mdi-account mdi-dark mdi-18px"
                class="font-weight-bold"
                outlined
                dense
                clearable
                :readonly="supply"
              ></v-autocomplete>
            </v-col>

            <v-col cols="12" sm="4" class="mb-0 py-0">
              <v-text-field
                v-model="form.additional_info"
                color="#007bff"
                :label="
                  $vuetify.lang.t(
                    '$vuetify.supplies.cols.additional_info.title'
                  )
                "
                prepend-inner-icon="mdi-script-text mdi-dark mdi-18px"
                class="font-weight-bold"
                outlined
                dense
                :readonly="supply"
              ></v-text-field>
            </v-col>

            <v-col
              cols="12"
              sm="3"
              class="mb-0 py-0"
              v-if="this.form.bill_type === 'PHARMACY'"
            >
              <v-autocomplete
                v-model="form.storage_depots"
                :items="itemsDepot"
                :loading="isLoadingDepot"
                :search-input.sync="searchDepot"
                color="#007bff"
                item-text="name"
                item-value="id"
                :label="
                  $vuetify.lang.t('$vuetify.supplies.cols.storage_depot.title')
                "
                prepend-inner-icon="mdi-archive mdi-dark mdi-18px"
                class="font-weight-bold"
                outlined
                dense
                clearable
                :readonly="supply"
              ></v-autocomplete>
            </v-col>
            <v-col
              cols="12"
              sm="3"
              class="mb-0 py-0"
              v-if="this.form.bill_type === 'PHARMACY'"
            >
              <v-select
                v-model="form.bill_shape"
                color="#007bff"
                :items="optionShape.type_shape"
                :label="$vuetify.lang.t('$vuetify.bills.cols.bill_shape.title')"
                :placeholder="
                  $vuetify.lang.t('$vuetify.bills.cols.bill_shape.title')
                "
                prepend-inner-icon="mdi-gender-male-female mdi-dark mdi-18px"
                class="font-weight-bold"
                outlined
                dense
              ></v-select>
            </v-col>
            <!--            <v-col-->
            <!--              cols="12"-->
            <!--              sm="1"-->
            <!--              class="mb-0 py-0"-->
            <!--              v-if="this.form.id === null"-->
            <!--            >-->
            <!--              <v-btn-->
            <!--                color="#007bff"-->
            <!--                :loading="loadingBtn"-->
            <!--                dark-->
            <!--                type="submit"-->
            <!--                dense-->
            <!--                max-width="2px"-->
            <!--                style="margin-top: 2px"-->
            <!--                depressed-->
            <!--                :disabled="-->
            <!--                  this.form.patient === null ||-->
            <!--                    this.form.doctor === null ||-->
            <!--                    this.form.patient_type === null ||-->
            <!--                    this.form.additional_info === null ||-->
            <!--                    supply-->
            <!--                "-->
            <!--              >-->
            <!--                <v-icon dark>-->
            <!--                  mdi-content-save-->
            <!--                </v-icon>-->
            <!--                &lt;!&ndash;{{ $vuetify.lang.t(messages.submit) }}&ndash;&gt;-->
            <!--              </v-btn>-->
            <!--            </v-col>-->
          </v-row>
        </v-card-text>
        <!--<v-card-actions style="margin-top: -45px">-->
        <!--<v-spacer></v-spacer>-->
        <!--<v-btn color="#007bff" :loading="loading" dark type="submit" small style="margin-left: 18px">-->
        <!--{{ $vuetify.lang.t(messages.submit) }}-->
        <!--</v-btn>-->
        <!--</v-card-actions>-->
        <v-divider class="mx-4" style="margin-top: -15px"></v-divider>
      </v-form>

      <v-form @submit.prevent="saveDetails" >
        <v-card-text >
          <div class="d-flex wrap align-center" style="width: 3500px;">
            <v-autocomplete
              v-model="form.details_stock"
              :items="itemsProduct"
              :loading="isLoading"
              :search-input.sync="search"
              color="#007bff"
              item-text="product_name"
              item-value="id"
              return-object
              :label="
                $vuetify.lang.t('$vuetify.supplies.cols.product_name.title')
              "
              class="font-weight-bold"
              outlined
              dense
              clearable
              style="max-width: 250px"
              :readonly="form.storage_depots === null"
              v-if="this.form.bill_type === 'PHARMACY'"
            ></v-autocomplete>
            <v-autocomplete
              v-model="form.medical_act"
              :items="itemsMedical_act"
              :loading="isLoadingMedical_act"
              :search-input.sync="searchMedical_act"
              color="#007bff"
              item-text="name"
              item-value="id"
              return-object
              style="max-width: 200px"
              :label="$vuetify.lang.t('$vuetify.bills.cols.medical_act.title')"
              class="font-weight-bold ml-1"
              outlined
              dense
              clearable
              v-if="this.form.bill_type === 'MEDICAL_ACT'"
            ></v-autocomplete>
            <v-text-field
              color="#007bff"
              :label="
                $vuetify.lang.t('$vuetify.supplies.cols.product_code.title')
              "
              class="font-weight-bold ml-1"
              outlined
              dense
              style="max-width: 120px"
              v-model="get_product_code"
              readonly
              filled
              v-if="this.form.bill_type === 'PHARMACY'"
            ></v-text-field>
            <v-text-field
              color="#007bff"
              :label="
                $vuetify.lang.t('$vuetify.bills.cols.medical_act.titleCode')
              "
              class="font-weight-bold ml-1"
              outlined
              dense
              v-if="this.form.bill_type === 'MEDICAL_ACT'"
              style="max-width: 120px"
              v-model="get_act_code"
              readonly
              filled
            ></v-text-field>
            <v-text-field
              v-model="form.quantity_ordered"
              color="#007bff"
              :label="
                $vuetify.lang.t('$vuetify.bills.cols.quantity_ordered.title')
              "
              class="font-weight-bold ml-1"
              style="max-width: 100px"
              outlined
              dense
              type="number"
              v-if="this.form.bill_shape === 'ORDINANCE'"
            ></v-text-field>
            <v-text-field
              v-model="form.quantity_served"
              color="#007bff"
              :label="
                $vuetify.lang.t('$vuetify.bills.cols.quantity_served.title')
              "
              class="font-weight-bold ml-1"
              outlined
              dense
              type="number"
              style="max-width: 100px"
              v-if="
                this.form.bill_type === 'MEDICAL_ACT' ||
                  this.form.bill_type === 'PHARMACY'
              "
            ></v-text-field>
            <v-text-field
              color="#007bff"
              class="font-weight-bold ml-1"
              outlined
              dense
              filled
              readonly
              style="max-width: 90px"
              v-if="this.form.bill_shape === 'CLASSIC'"
            ></v-text-field>
            <v-text-field
              color="#007bff"
              :label="$vuetify.lang.t('$vuetify.bills.cols.pub.title')"
              class="font-weight-bold ml-1"
              outlined
              dense
              v-model="get_product_public_price"
              readonly
              filled
              style="max-width: 130px"
              v-if="this.form.bill_type === 'PHARMACY'"
            ></v-text-field>
            <v-text-field
              color="#007bff"
              :label="$vuetify.lang.t('$vuetify.bills.cols.price.title')"
              class="font-weight-bold ml-1"
              outlined
              dense
              v-model="get_act_price"
              readonly
              filled
              style="max-width: 110px"
              v-if="this.form.bill_type === 'MEDICAL_ACT'"
            ></v-text-field>
            <v-text-field
              color="#007bff"
              :label="$vuetify.lang.t('$vuetify.bills.cols.delivery.title')"
              class="font-weight-bold ml-1"
              outlined
              dense
              v-model="form.delivery"
              v-if="this.form.bill_type === 'PHARMACY'"
              style="max-width: 130px"
            ></v-text-field>
            <v-text-field
              color="#007bff"
              :label="$vuetify.lang.t('$vuetify.bills.cols.pun.title')"
              class="font-weight-bold ml-1"
              outlined
              dense
              readonly
              filled
              v-model="get_pu_net"
              v-if="this.form.bill_type === 'PHARMACY'"
              style="max-width: 155px"
            ></v-text-field>
            <v-text-field
              v-model="get_amount_gross"
              color="#007bff"
              :label="$vuetify.lang.t('$vuetify.bills.cols.amount_gross.title')"
              class="font-weight-bold ml-1"
              outlined
              :value="get_amount_gross"
              dense
              readonly
              filled
              v-if="this.form.bill_type === 'PHARMACY'"
              style="max-width: 200px"
            ></v-text-field>
            <v-text-field
              v-model="get_amount_net"
              color="#007bff"
              :label="$vuetify.lang.t('$vuetify.bills.cols.amount_net.title')"
              class="font-weight-bold ml-1"
              outlined
              :value="get_amount_net"
              dense
              readonly
              filled
              v-if="this.form.bill_type === 'PHARMACY'"
              style="max-width: 200px"
            ></v-text-field>
            <v-tooltip top v-if="form.bill_type === 'PHARMACY'">
              <template v-slot:activator="{ on }">
                <template>
                  <v-btn
                    icon
                    color="#007bff"
                    :loading="loadingBtn"
                    type="submit"
                    v-on="on"
                    style="margin-top: -25px"
                    class="font-weight-bold ml-5"
                    :disabled="
                      form.patient === null ||
                        form.doctor === null ||
                        form.additional_info === null ||
                        form.storage_depots === null ||
                        supply
                    "
                  >
                    <v-icon size="30" dark
                      >mdi-plus-circle-multiple-outline mdi-30px</v-icon
                    >
                  </v-btn>
                </template>
              </template>
              <span
                class="
                        text-center
                        font-weight-bold
                        body-2
                        withe--text
                        text--d
                        ken-1
                      "
              >
                {{ $vuetify.lang.t("$vuetify.bills.new.add_product") }}
              </span>
            </v-tooltip>
            <v-tooltip top v-else>
              <template v-slot:activator="{ on }">
                <template>
                  <v-btn
                    icon
                    color="#007bff"
                    :loading="loadingBtn"
                    type="submit"
                    v-on="on"
                    style="margin-top: -25px"
                    class="font-weight-bold ml-5"
                    :disabled="
                      form.patient === null ||
                        form.doctor === null ||
                        form.additional_info === null ||
                        form.additional_info === null ||
                        form.medical_act === null ||
                        supply
                    "
                  >
                    <v-icon size="30" dark
                      >mdi-plus-circle-multiple-outline mdi-30px</v-icon
                    >
                  </v-btn>
                </template>
              </template>
              <span
                class="
                        text-center
                        font-weight-bold
                        body-2
                        withe--text
                        text--d
                        ken-1
                      "
              >
                {{ $vuetify.lang.t("$vuetify.bills.new.add_act") }}
              </span>
            </v-tooltip>
          </div>
        </v-card-text>
        <v-divider class="mx-4" style="margin-top: -15px"></v-divider>
      </v-form>
      <v-form style="margin-top: 15px">
        <v-card-text>
          <v-row wrap>
            <v-col cols="12" sm="4" class="mb-0 py-0">
              <v-text-field
                color="#007bff"
                :label="
                  $vuetify.lang.t('$vuetify.bills.cols.net_payable.title')
                "
                class="font-weight-bold"
                outlined
                dense
                :value="get_bills_amount"
                readonly
                filled
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="4" class="mb-0 py-0">
              <v-text-field
                v-model="form.advances"
                :value="get_advances"
                color="#007bff"
                :label="$vuetify.lang.t('$vuetify.bills.cols.advance.title')"
                class="font-weight-bold"
                outlined
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="4" class="mb-0 py-0">
              <v-text-field
                v-model="get_balance"
                :value="get_balance"
                color="#007bff"
                :label="$vuetify.lang.t('$vuetify.bills.cols.balance.title')"
                class="font-weight-bold"
                outlined
                dense
                readonly
                filled
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
        <v-divider class="mx-4" style="margin-top: -15px"></v-divider>
      </v-form>
      <div>
        <v-card max-width="1690px" style="margin-left: 8px">
          <div class="mt-5 mx-5">
            <v-btn
              color="#007bff"
              @click="save()"
              class="mr-2 my-2 mx-1"
              depressed
              dense
              dark
              :loading="loadingBtn"
              style="margin-left: 1080px"
              :disabled="this.get_bills_amount === 0 || form.advances === null"
            >
              <v-icon>
                mdi-content-save
              </v-icon>
              {{ $vuetify.lang.t("$vuetify.bills.new.save_bills") }}
            </v-btn>
            <v-btn
              color="#007bff"
              @click="save_print_bills()"
              class="mr-2 my-1 mx-6"
              depressed
              dense
              dark
              :loading="loadingBtn"
              style="margin-left: 1080px"
              :disabled="form.advances === null"
            >
              <v-icon>
                mdi-content-save
              </v-icon>
              {{ $vuetify.lang.t("$vuetify.bills.new.save_print_bills") }}
            </v-btn>
            <v-spacer></v-spacer>
          </div>
          <v-data-table
            :footer-props="{
              'items-per-page-options': itemPerPageOptions
            }"
            :headers="headers"
            :items="items"
            :items-per-page="itemPerPage"
            :loading="loadingDetails.list"
            :options.sync="pagination"
            :server-items-length="meta.totalElements"
            :sort-by="sortBy"
            :sort-desc="sortDesc"
            item-key="name"
            class="mt-5"
          >
            <template v-slot:header.product_name="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <template v-slot:header.product_code="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <template v-slot:header.quantity_served="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <template v-slot:header.pub="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <template v-slot:header.pun="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr> </template
            ><template v-slot:header.delivery="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr> </template
            ><template v-slot:header.amount_gross="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr> </template
            ><template v-slot:header.amount_net="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <template v-slot:header.null="{ header }">
              <tr>
                {{
                  $vuetify.lang.t(header.text)
                }}
              </tr>
            </template>
            <template v-slot:body="{ items }">
              <v-hover>
                <tbody slot-scope="{ hover }">
                  <tr :key="item.id" v-for="item in items">
                    <td class="subtitle-2">
                      {{ item.details_stock.product.name }}
                    </td>
                    <td class="subtitle-2">
                      {{ item.details_stock.product.code }}
                    </td>

                    <td class="subtitle-2">
                      {{ item.quantity_served }}
                    </td>
                    <td class="subtitle-2">
                      {{ item.pub }}
                    </td>
                    <td class="subtitle-2">
                      {{ item.delivery }}
                    </td>
                    <td class="subtitle-2">
                      {{ item.pun }}
                    </td>
                    <td class="subtitle-2">
                      {{ item.amount_gross }}
                    </td>
                    <td class="subtitle-2">
                      {{ item.amount_net }}
                    </td>
                    <td
                      class="subtitle-2"
                      v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"
                      width="50px"
                    >
                      <template>
                        <v-tooltip bottom>
                          <template
                            v-slot:activator="{ on }"
                            v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"
                          >
                            <v-btn
                              @click="editDetails(item)"
                              color="secondary"
                              icon
                              text
                              v-on="on"
                              v-show="$vuetify.breakpoint.smAndDown || hover"
                            >
                              <v-icon mdi-18px>mdi-grease-pencil</v-icon>
                            </v-btn>
                          </template>
                          <span>{{
                            $vuetify.lang.t("$vuetify.btn.edit")
                          }}</span>
                        </v-tooltip>
                        <v-tooltip bottom>
                          <template
                            v-slot:activator="{ on }"
                            v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"
                          >
                            <v-btn
                              @click="deleteItem(item)"
                              color="red"
                              icon
                              text
                              v-on="on"
                              dark
                              v-show="$vuetify.breakpoint.smAndDown || hover"
                            >
                              <v-icon mdi-18px>mdi-delete-outline</v-icon>
                            </v-btn>
                          </template>
                          <span>{{
                            $vuetify.lang.t("$vuetify.btn.remove")
                          }}</span>
                        </v-tooltip>
                      </template>
                    </td>
                    <td class="subtitle-2" v-else></td>
                  </tr>
                </tbody>
              </v-hover>
            </template>
          </v-data-table>
          <delete-item
            :dialog="dialogDelete"
            :messages="messagesDelete"
            :urlItems="urlDeleteItems"
            @getItemsDetails="getItemsDetails"
          ></delete-item>
        </v-card>
      </div>
    </v-card>
    <form-cmv :dialog="dialogFormCMV"></form-cmv>
    <form-patient :dialog="dialogForm"></form-patient>
    <form-settlement :dialog="dialogFormSet"></form-settlement>
  </v-dialog>
</template>

<script>
import FormCmv from "./../bills/FormCashMovement";
import FormSettlement from "./../bills/FormSettlement";
import FormPatient from "./../bills/FormPatient";

// import ListMixin from "./../../mixins/Common/List.vue";
import FormMixin from "./../../mixins/Common/Form.vue";
// import {mapGetters} from "vuex";
// import { required } from "vuelidate/lib/validators";
import DeleteItem from "./../Common/DeleteDetails";
const FileSaver = require("file-saver");

export default {
  mixins: [FormMixin],
  props: {
    form: {
      type: Object,
      default: function() {
        return {
          id: null,
          patient: null,
          doctor: null,
          medical_act: null,
          patient_name: null,
          patient_balance: null,
          net_payable: null,
          patient_type: null,
          bills_date: null,
          pub: null,
          pun: null,
          amount_gross: null,
          quantity_ordered: null,
          amount_net: null,
          total_amount: null,
          quantity_served: null,
          details_stock: null,
          delivery: null,
          balance: null,
          bills: null,
          reference_no: null,
          additional_info: null,
          bills_amount: null,
          code: null,
          cashier: null,
          bill_type: null,
          advances: null,
          bill_shape: null,
          storage_depots: null
        };
      }
    }
  },
  data: () => ({
    formData: {
      id: null,
      name: null,
      coefficient: null,
      number_account: null,
      quote_internal: null,
      quote_external: null
    },
    headers: [],
    items: [],
    meta: {},
    sum: "",
    next_beneficiary: "",
    current_contribution: "",
    itemPerPage: 10,
    //itemPerPages: 3,
    itemPerPageOptions: [5, 10, 30],
    urlDeleteItems: "",
    sortDesc: true,
    loading: {
      list: false,
      refresh: false,
      filter: false
    },
    pagination: {},
    selectedItem: false,
    _search: {},
    _sorts: {},
    dialogDelete: {
      show: false
    },
    sortBy: ["id"],
    e1: 1,
    isLoadingDepot: false,
    isLoadingPatient: false,
    isLoadingDoctor: false,
    isLoadingMedical_act: false,
    isLoading: false,
    search: null,
    searchDepot: null,
    searchMedical_act: null,
    searchPatient: null,
    searchDoctor: null,
    menu2: false,
    supply: false,
    active: false,
    menu3: false,
    menu4: false,
    menu: false,
    dialogFormCMV: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    dialogFormSet: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },

    dialogFormDetails: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    optionBills: {
      type_bills: []
    },
    optionShape: {
      type_shape: []
    },
    optionPatient_shape: {
      type_Patient_shape: []
    },
    messagesDelete: {
      success: "$vuetify.supplies.delete.success"
    },
    loadingBtn: false,
    // formR: {
    //   id: null,
    //   name: null
    // },
    loadingDetails: {
      list: false,
      refresh: false,
      filter: false
    },
    itemsDoctor: [],
    itemsPatient: [],
    itemsDepot: [],
    itemsProduct: [],
    itemsMedical_act: [],
    urlDepot: "storage_depots",
    urlPatient: "patients",
    urlDoctor: "doctors",
    urlProduct: "products/get_products",
    urlMedical_act: "medical_act",
    urlItems: "bills",
    urlItemsDetails_bills: "details_bills"
  }),
  watch: {
    pagination: {
      handler() {
        this.getItemsDetails();
      },
      deep: true
    },
    searchMedical_act(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoading = true;
      this.$store
        .dispatch("request", {
          url: self.urlMedical_act,
          params: params
        })
        .then(response => {
          self.itemsMedical_act = response.data.content;
          // console.log(this.form.product);
        })
        .finally(() => (this.isLoading = false));
    },
    search(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoading = true;
      this.$store
        .dispatch("request", {
          url: self.urlProduct + "?storage_depots=" + this.form.storage_depots,
          params: params
        })
        .then(response => {
          self.itemsProduct = response.data.content;
          console.log(response.data.content);
        })
        .finally(() => (this.isLoading = false));
    },
    searchPatient(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoadingPatient = true;
      this.$store
        .dispatch("request", {
          url: self.urlPatient,
          params: params
        })
        .then(response => {
          // console.log("respp", response.data.content);
          self.itemsPatient = response.data.content;
        })
        .finally(() => (this.isLoadingPatient = false));
    },
    searchDoctor(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoadingDoctor = true;
      this.$store
        .dispatch("request", {
          url: self.urlDoctor,
          params: params
        })
        .then(response => {
          // console.log("respp", response.data.content);
          self.itemsDoctor = response.data.content;
        })
        .finally(() => (this.isLoadingDoctor = false));
    },
    searchDepot(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoadingDepot = true;
      this.$store
        .dispatch("request", {
          url: self.urlDepot,
          params: params
        })
        .then(response => {
          // console.log("respp", response.data.content);
          self.itemsDepot = response.data.content;
        })
        .finally(() => (this.isLoadingDepot = false));
    }
  },
  created() {
    this.setHeaders();
    this.getOptionsBill();
    this.getOptionsShape();
    this.getOptionsPatient_shape();

    //this.setItemRoles();
  },
  validations() {
    let validators = {
      form: {}
    };
    return validators;
  },
  methods: {
    getOptionsPatient_shape() {
      // On pouvait aussi utliser le forEach au lieu de   _.eaach
      let self = this;
      this._.each(
        this.$store.getters.type_Patient_shape,
        type_Patient_shape => {
          self.optionPatient_shape.type_Patient_shape.push({
            text: self.$vuetify.lang.t(type_Patient_shape.text),
            value: type_Patient_shape.value
          });
        }
      );
    },
    getOptionsBill() {
      // On pouvait aussi utliser le forEach au lieu de   _.eaach
      let self = this;
      this._.each(this.$store.getters.type_bills, type_bills => {
        self.optionBills.type_bills.push({
          text: self.$vuetify.lang.t(type_bills.text),
          value: type_bills.value
        });
      });
    },
    getOptionsShape() {
      // On pouvait aussi utliser le forEach au lieu de   _.eaach
      let self = this;
      this._.each(this.$store.getters.type_shape, type_shape => {
        self.optionShape.type_shape.push({
          text: self.$vuetify.lang.t(type_shape.text),
          value: type_shape.value
        });
      });
    },
    forceRefresh() {
      this.dialog.show = false;
      this.items = [];
      this.active = false;
    },
    deleteItem(item) {
      this.urlDeleteItems = this.urlItemsDetails_bills + "/" + item.id;
      this.dialogDelete.show = true;
    },
    setHeaders() {
      this.headers = [
        {
          text: "$vuetify.bills.cols.product_name.title",
          value: "product_name",
          align: "start",
          sortable: false,
          width: "140",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.bills.cols.product_code.title",
          value: "product_code",
          align: "start",
          sortable: false,
          width: "20",
          class: "grey--text text--darken-3"
        },

        {
          text: "$vuetify.bills.cols.quantity_served.title",
          value: "quantity_served",
          align: "start",
          sortable: false,
          width: "100",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.bills.cols.pub.title",
          value: "pub",
          align: "start",
          sortable: false,
          width: "100",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.bills.cols.delivery.title",
          value: "delivery",
          align: "start",
          sortable: false,
          width: "100",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.bills.cols.pun.title",
          value: "pun",
          align: "start",
          sortable: false,
          width: "100",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.bills.cols.amount_gross.title",
          value: "amount_gross",
          align: "start",
          sortable: false,
          width: "100",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.bills.cols.amount_net.title",
          value: "amount_net",
          align: "start",
          sortable: false,
          width: "60",
          class: "grey--text text--darken-3"
        },

        // {
        //   text: "$vuetify.supplies.cols.created.title",
        //   value: "create_date",
        //   align: "start",
        //   sortable: false,
        //   width: "105",
        //   class: "grey--text text--darken-3"
        // },
        {
          text: "$vuetify.supplies.cols.actions.title",
          value: null,
          align: "start",
          sortable: false,
          width: "70"
        }
      ];
    },
    createPatient() {
      this.dialogForm.show = true;
      this.formData = {
        id: null,
        name: null,
        coefficient: null,
        number_account: null,
        quote_internal: null,
        quote_external: null
      };
    },
    createD() {
      this.dialogFormCMV.show = true;
      this.formData = {
        id: null,
        name: null,
        coefficient: null,
        number_account: null,
        quote_internal: null,
        quote_external: null
      };
    },
    createP() {
      this.dialogFormSet.show = true;
      this.formData = {
        id: null,
        name: null,
        coefficient: null,
        number_account: null,
        quote_internal: null,
        quote_external: null
      };
    },
    // Fonction pour rcuperer le contenu des champs du formulaire
    getDataForm() {
      let data = {
        doctor: this.form.doctor["id"],
        patient: this.form.patient["id"],
        cashier: this.$auth.user().id,
        patient_type: this.form.patient_type,
        bills_date: this.form.bills_date,
        bill_type: this.form.bill_type,
        bill_shape: this.form.bill_shape,
        additional_info: this.form.additional_info,
        storage_depots: this.form.storage_depots,
        net_payable: this.get_bills_amount,
        advance: this.get_advances,
        balance: this.get_balance
      };
      return data;
    },
    resetDataForm() {
      this.form.id = null;
      this.form.patient = null;
      this.form.medical_act = null;
      this.form.doctor = null;
      this.items = [];
      if (this.form.id) return;
      let self = this;
      this.$v.form.$reset();
      this._.each(this.form, (field, k) => {
        if (typeof self.form[k] === "object") {
          self._.each(self.form[k], (subField, i) => {
            if (typeof self.form[k][i] === "boolean") {
              self.form[k][i] = false;
            } else {
              self.form[k][i] = null;
            }
          });
        } else {
          if (typeof self.form[k] === "boolean") {
            self.form[k] = false;
          } else {
            self.form[k] = null;
          }
        }
      });
    },
    getDataFormdetails() {
      let data = {
        quantity_served: this.form.quantity_served,
        details_stock: this.form.details_stock["id"],
        quantity_ordered:
          this.form.quantity_ordered === null ? 0 : this.form.quantity_ordered,
        pub: this.get_product_public_price,
        pun: this.get_pu_net,
        delivery:
          this.form.delivery === null || this.form.delivery === ""
            ? 0
            : this.form.delivery,
        amount_gross: this.get_amount_gross,
        amount_net: this.get_amount_net,
        storage_depots: this.form.storage_depots,
        createdAt: this.form.bills_date,
        medical_act:
          this.form.medical_act === undefined || this.form.medical_act === null
            ? ""
            : this.form.medical_act["id"]
      };

      return data;
    },
    setForm(item) {
      // Donnes envoyes lors de la modification
      this.form.id = item.id;
      this.form.bills = item.id;
      this.form.code = item.code;
      this.form.storage_depots = item.storage_depots["id"];
      this.itemsDepot.push(item.storage_depots);
      this.form.patient = item.patient["id"];
      this.itemsPatient.push(item.patient);
      this.form.doctor = item.doctor["id"];
      this.itemsDoctor.push(item.doctor);
      this.form.patient_type = item.patient_type;
      this.form.details_stock = item.details_stock;
      this.form.bills_date = item.bills_date;
      this.form.bill_type = item.bill_type;
      this.form.bill_shape = item.bill_shape;
      this.form.additional_info = item.additional_info;
      this.getItemsDetails();
      // console.log("cul", this.form.supplies);
    },
    getRouteDetails() {
      if (this.form.id) {
        return this.urlItemsDetails_bills + "/" + this.form.id;
      } else {
        return this.urlItemsDetails_bills;
      }
    },
    getRoute() {
      if (this.form.id) {
        return this.urlItems + "/" + this.form.id;
      } else {
        return this.urlItems;
      }
    },
    refreshItemsDetail() {
      let self = this;
      this.loadingDetails.refresh = true;
      this.getItemsDetails()
        .then(() => {})
        .catch(() => {})
        .then(() => {
          self.loadingDetails.refresh = false;
        });
    },
    save() {
      this.$store.dispatch("showNotification", {
        statut: false
      });
      let self = this;
      self.$v.$touch();
      if (!self.$v.$invalid) {
        this.loading = true;
        this.$store
          .dispatch("request", {
            url: self.getRoute(),
            method: self.getMethod(),
            data: self.getDataForm()
          })
          .then(() => {
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(self.messages.success)
            });
            self.$emit("getItems");
            self.resetDataForm();
          })
          .catch(error => {
            let message;
            if (error.response.status === 400) {
              message = "$vuetify.error_occured";
              const fields = error.response.data;
              self.setFormErrors(fields);
              this.$forceUpdate();
            } else {
              message = "$vuetify.error_server";
            }
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(message),
              color: "#B71C1C"
            });
          })
          .then(() => {
            self.loading = false;
          });
      }
      /*else {
               console.log(self.$v);
             }*/
    },
    save_print_bills() {
      this.$store.dispatch("showNotification", {
        statut: false
      });
      let self = this;
      self.$v.$touch();
      if (!self.$v.$invalid) {
        this.loading = true;
        this.$store
          .dispatch("request", {
            url: self.getRoute() + "?print=1",
            method: self.getMethod(),
            data: self.getDataForm(),
            responseType: "blob"
          })
          .then(response => {
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(self.messages.success)
            });
            let fileName = "Facture" + new Date().toISOString().substr(0, 10);
            let blob = new Blob([response.data], { type: response.data.type });
            FileSaver.saveAs(blob, fileName);
            self.$emit("getItems");
            self.resetDataForm();
          })
          .catch(error => {
            let message;
            if (error.response.status === 400) {
              message = "$vuetify.error_occured";
              const fields = error.response.data;
              self.setFormErrors(fields);
              this.$forceUpdate();
            } else {
              message = "$vuetify.error_server";
            }
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(message),
              color: "#B71C1C"
            });
          })
          .then(() => {
            self.loading = false;
          });
      }
      /*else {
               console.log(self.$v);
             }*/
    },
    getItemsDetails() {
      let self = this;
      const { page, itemsPerPage } = this.pagination;
      // console.log(page);
      // console.log(itemsPerPage);
      let params = {
        page: page,
        size: itemsPerPage
      };
      if (!this._.isEmpty(this._search)) {
        this._search = this._.merge(params, this._search);
      }
      return new Promise((resolve, reject) => {
        this.loadingDetails.list = true;
        //console.log("ls params", params);
        //console.log("Sorts", sortBy, sortDesc);
        self.$store
          .dispatch("request", {
            url: self.urlItemsDetails_bills + "?bills=" + this.form.bills,
            params: params
          })
          .then(response => {
            let data = response.data;
            self.items = data.content;
            self.meta = { totalElements: data.totalElements };
            // console.log("meta", data);
            resolve(response);
          })
          .catch(err => {
            let message = this.$vuetify.lang.t("$vuetify.error_occured");
            if (err.response) {
              if (err.response.status === 400) {
                self.setApiFormErrors(err.response.data.data);
                message = self.$vuetify.lang.t("$vuetify.error_form");
              } else if (err.response.status === 403) {
                message = self.$vuetify.lang.t("$vuetify.error_denied");
              } else if (err.response.status === 500) {
                message = self.$vuetify.lang.t("$vuetify.error_server");
              }
            }
            self.$store.dispatch("showNotification", {
              status: true,
              text: message
            });
            reject(err);
          })
          .then(() => {
            self.loadingDetails.list = false;
            resolve();
          });
      });
    },
    editDetails(item) {
      this.form.details_stock = item.details_stock;
    },
    saveDetails() {
      // console.log(this.getDataFormdetails());
      this.$store.dispatch("showNotification", {
        statut: false
      });
      let self = this;
      self.$v.$touch();
      if (!self.$v.$invalid) {
        this.loading = true;
        this.$store
          .dispatch("request", {
            url: self.getRouteDetails(),
            method: self.getMethod(),
            data: self.getDataFormdetails()
          })
          .then(() => {
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t("$vuetify.supplies.new.successDetails")
            });
            //console.log("Quantity", this.form.quantity);
            this.getItemsDetails();
          })
          .catch(error => {
            let message;
            if (error.response) {
              if (error.response.status === 400) {
                message = "$vuetify.error_occured";
                const fields = error.response.data;
                self.setFormErrors(fields);
                this.$forceUpdate();
              } else {
                message = "$vuetify.error_server";
              }
            }
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(message),
              color: "#B71C1C"
            });
          })
          .then(() => {
            self.loading = false;
          });
      }
      /*else {
               console.log(self.$v);
             }*/
    }
  },
  computed: {
    get_patient_code() {
      let patient_code = "";
      patient_code = this.form.patient === null ? "" : this.form.patient?.code;
      return patient_code;
    },
    get_patient_shape() {
      let patient_shape = "";
      patient_shape =
        this.form.patient === null ? "" : this.form.patient?.shape;
      return patient_shape;
    },
    get_product_code() {
      console.log(this.form.details_stock);
      let product_code = "";
      product_code =
        this.form.details_stock === null ||
        this.form.details_stock === undefined
          ? ""
          : this.form.details_stock?.product.code;
      let product_stock = "";
      product_stock =
        this.form.details_stock === null ||
        this.form.details_stock === undefined
          ? ""
          : this.form.details_stock?.qte_stock;
      let code_stock = "";
      code_stock = product_code + "  /  " + product_stock;
      return code_stock;
    },
    get_act_code() {
      let act_code = "";
      act_code =
        this.form.medical_act === null ? "" : this.form.medical_act?.code;
      return act_code;
    },
    get_product_public_price() {
      let product_public_price = "";
      product_public_price =
        this.form.details_stock === null
          ? ""
          : this.form.details_stock?.product.public_price;
      return product_public_price;
    },
    get_act_price() {
      let act_price = "";
      act_price =
        this.form.medical_act === null ? "" : this.form.medical_act?.price;
      return act_price;
    },
    get_product_price() {
      let product_price = "";
      product_price =
        this.form.details_stock === null
          ? ""
          : this.form.details_stock?.product.purchase_price;
      return product_price;
    },
    get_bills_amount() {
      let total = 0;
      this.items.forEach(d => {
        total += d.amount_net;
      });
      // let sum = this.items.reduce((a, b) => a + (b[key] || 0), 0)
      // console.log("hum",total);
      return total;
    },
    get_pu_net() {
      // console.log("hum",this.form.product.purchase_price);
      if (this.form.delivery === undefined || this.form.delivery === null) {
        return 0;
      } else {
        let pun = "";
        pun = this.get_product_public_price * this.form.delivery;
        let PUN = "";
        PUN = pun / 100;
        let PUNT = "";
        PUNT = this.get_product_public_price - PUN;
        return PUNT;
      }
    },
    get_amount_gross() {
      // console.log("hum",this.form.product.purchase_price);
      if (
        this.form.quantity_served === undefined ||
        this.get_product_public_price === undefined
      ) {
        return 0;
      } else {
        let amount_gross = "";
        amount_gross =
          this.form.quantity_served * this.get_product_public_price;
        return amount_gross;
      }
    },
    get_amount_net() {
      // console.log("hum",this.form.product.purchase_price);
      if (this.form.quantity_served === undefined || this.get_pu_net === 0) {
        return this.get_amount_gross;
      } else {
        let amount_net = "";
        amount_net = this.get_pu_net * this.form.quantity_served;
        return amount_net;
      }
    },
    get_balance() {
      // console.log("hum",this.form.product.purchase_price);
      if (this.form.advances === undefined) {
        return this.get_bills_amount;
      } else {
        let balance = "";
        balance = this.get_bills_amount - this.form.advances;
        return balance;
      }
    },
    get_advances() {
      // console.log("hum",this.form.product.purchase_price);
      if (this.form.advances === undefined) {
        return 0;
      } else {
        return this.form.advances;
      }
    },
    // get_storage_depot() {
    //   if (this.form.arrival_date) {
    //     const today = new Date();
    //     const date_year = today.getFullYear();
    //     return (
    //       date_year - moment(String(this.form.arrival_date)).format("YYYY")
    //     );
    //   } else {
    //     return 0;
    //   }
    // },
    // Gestion des erreurs du formulaire

    nameErrors() {
      let errors = [];
      if (!this.$v.form.name.$dirty) return errors;
      !this.$v.form.name.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.supplies.cols.name.required")
        );
      // !this.$v.form.name.unique &&
      //   errors.push(
      //     this.$vuetify.lang.t("$vuetify.supplies.cols.name.unique")
      //   );
      return errors;
    },
    reference_noErrors() {
      let errors = [];
      if (!this.$v.form.reference_no.$dirty) return errors;
      !this.$v.form.reference_no.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.supplies.cols.reference_no.required")
        );
      return errors;
    },
    total_amountErrors() {
      let errors = [];
      if (!this.$v.form.total_amount.$dirty) return errors;
      !this.$v.form.total_amount.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.supplies.cols.total_amount.required")
        );
      return errors;
    }
  },
  components: {
    DeleteItem,
    FormCmv,
    FormSettlement,
    FormPatient
  }
};
</script>
